@media (max-width: 1200px) {
	.menu {
		&_item {
			padding-right: 20px; }
		&_link {
			font-size: 13px; } }
	.promo {
		&_header {
			font-size: 50px; } }
	.reasons {
		&_block {
			align-items: flex-start; }
		&_descr {
			width: 300px; } }

	.mobile {
		&_item {
			width: 290px;
			&_subtitle {
				font-size: 22px; } } }
	.choice {
		padding: 55px 0;
		min-height: 840px; }
	.require {
		br {
			display: none; } }
	.footer {
		&_wrapper > div {
			width: 50%;
			&:nth-child(1) {
				width: 25%; }
			&:nth-child(3) {
				width: 25%;
				display: flex;
				justify-content: flex-end; } }
		&_mobile {
			display: flex;
			a {
				margin: 0 3px;
				img {
					width: 100%; } } } } }


@media (max-width: 991px) {
	.menu {
		&_item {
			padding-right: 10px; }
		&_link {
			font-size: 10px; } }
	.subheader {
		&_logo {
			img {
				width: 100%; } }
		&_official {
			font-size: 11px; }
		&_phone {
			font-size: 18px; }
		&_btn {
			margin-top: 22px;
			height: 38px;
			width: 100%; } }
	.promo {
		&_header {
			font-size: 40px; }
		&_subheader {
			font-size: 30px; } }
	.reasons {
		&_block {
			flex-direction: column;
			align-items: center; }
		&_round {
			margin-right: 0; }
		&_descr {
			width: 300px;
			margin-top: 20px; } }

	.world {
		min-height: 650px;
		width: 100vw; }
	.footer {
		&_wrapper {
			flex-wrap: wrap; }
		&_wrapper > div {
			width: 100%;
			display: flex;
			justify-content: center;
			margin-bottom: 20px;
			&:nth-child(1) {
				width: 100%; }
			&:nth-child(3) {
				width: 100%;
				justify-content: center; } } } }

@media (max-width: 767px) {
	nav {
		width: 100vw; }
	.menu {
		position: fixed;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		top: 0;
		left: -100%;
		width: 50%;
		height: 100%;
		padding: 20px 15px;
		background-color: rgba(0, 0, 0, .8);
		transition: 0.6s all;
		&_active {
			left: 0; }
		&_item {
			padding-right: 0;
			border: none;
			margin: 10px; }
		&_link {
			font-size: 20px;
			text-decoration: none; } }
	.hamburger {
		display: block;
		position: absolute;
		right: 10px;
		top: 50%;
		transform: translateY(-50%);
		width: 26px;
		height: 12px;
		span {
			display: block;
			height: 2px;
			width: 100%;
			background-color: #fff;
			margin-bottom: 2px;
			transition: 0.5s all; }
		&_active {
			span {
				margin-bottom: -2px;
				&:nth-child(1) {
					transform: translateY(3px) rotate(-45deg); }
				&:nth-child(2) {
					display: none; }
				&:nth-child(3) {
					transform: translateY(3px) rotate(45deg); } } } }
	.xs-hidden {
		display: none; }
	.xs-visible {
		display: block; }
	.subheader {
		&_phone {
			display: block;
			margin-top: 10px;
			color: #FFF;
			font-size: 16px;
			font-weight: 400;
			text-transform: uppercase;
			text-align: center; }
		&_btn {
			display: block;
			width: 200px;
			height: 40px;
			margin: 0 auto;
			margin-top: 10px; } }
	.promo {
		&_header {
			line-height: 45px; } }

	.reasons {
		padding: 40px 0;
		&_descr {
			width: 350px; }
		&_subtitle {
			text-align: center; }
		&_text {
			margin-top: 10px; } }
	.mobile {
		padding: 40px 0; }
	.choice {
		min-height: 600px;
		padding: 40px 0; }
	.require {
		padding: 40px 0; }
	.world {
		min-height: 450px; } }


@media (max-width: 575px) {
	nav {
		height: 30px;
		width: 100vw; }
	header {
		height: 30px; }
	.subheader {
		height: 60px;
		top: 30px;
		&_official {
			display: none; }
		&_logo {
			margin-top: 18px; }
		&_phone {
			font-size: 12px;
			font-weight: 400;
			text-transform: uppercase;
			margin-top: 11px; }
		&_btn {
			width: 110px;
			height: 22px;
			font-size: 10px;
			margin-top: 6px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
	.promo {
		min-height: 400px;
		padding: 92px 0 40px 0;
		&_descr {
			display: none; }
		&_header {
			font-size: 30px;
			padding: 0 24px;
			line-height: 35px; }
		&_subheader {
			font-size: 20px;
			margin-top: 32px;
			padding: 0 12px;
			line-height: 30px; }
		&_btn {
			width: 210px;
			height: 50px;
			margin-top: 72px;
			font-size: 16px;
			&:hover {
				background-image: linear-gradient(to top, $main-color 0%, #4ce2ff 100%); } } }
	.menu {
		width: 85%;
		&_item {
			width: 100%;
			text-align: center;
			padding: 4px 0; }
		&_link {
			display: block; } }
	.reasons {
		padding: 22px 0 36px 0;
		&_block {
			margin-top: 24px; }
		&_round {
			width: 100px;
			height: 100px; }
		&_descr {
			width: 100%;
			margin-top: 24px; }
		&_subtitle {
			font-size: 16px; } }

	.title {
		font-size: 16px;
		margin-top: 15px;
		line-height: 16px; }
	.mobile {
		padding: 28px 0 43px 0;
		&_row {
			margin-top: 26px; }
		&_item {
			width: 290px;
			height: 254px;
			&_subtitle {
				font-size: 18px;
				padding: 0 32px 0 34px; } } }
	.subtitle {
		font-size: 14px;
		margin-top: 9px; }
	.choice {
		padding: 26px 0 0 0;
		min-height: 400px;
		.black {
			height: 400px; }
		.title {
			display: none; }
		.subtitle {
			display: none; }
		&_img {
			width: 300px;
			height: 190px;
			margin: 36px auto 0 auto; }
		&_descr {
			color: #FFF;
			font-size: 17px;
			line-height: 24px;
			z-index: 2;
			position: relative;
			padding: 0 22px; } }
	.require {
		padding: 24px 0 30px 0;
		&_block {
			margin: 30px 0 0 0; }
		&_title {
			font-size: 16px;
			line-height: 16px; }
		&_list {
			margin-top: 6px;
			padding-left: 32px;
			margin-bottom: 0; }
		li {
			font-size: 14px;
			line-height: normal;
			br {
				display: none; } }
		li:last-child {
			margin-bottom: 0; }
		&_descr {
			margin-top: 0;
			font-size: 14px;
			line-height: normal; } }
	.world {
		padding: 26px 0 29px 0;
		min-height: 360px;
		.subtitle {
			margin-top: 11px; }
		.world_link {
			line-height: 15px;
			min-width: 222px;
			text-decoration: none; } }
	footer {
		padding: 25px 0 20px 0; }
	.footer {
		&_divider {
			width: 253px;
			margin: 0 auto; }
		&_wrapper {
			padding-top: 22px; }
		&_wrapper > div {
			margin-bottom: 20px;
			&:nth-child(1) {
				order: 2; }
			&:nth-child(2) {
				order: 1; }
			&:nth-child(3) {
				order: 3; } }
		&_links {
			width: 290px;
			&_main {
				padding: 0;
				font-size: 14px; }
			&_sub {
				flex-wrap: wrap;
				justify-content: center;
				a {
					margin: 0 7px; } }
			&_lang {
				margin-top: 10px; } } } }




